<template>
    <div id="editDir">
        <ul class="collection with-header">
            <li class="collection-header"><h4> {{Nombre}} </h4> </li>
            <li class="collection-item">Empleado id: {{empleado_id}} </li>
            <li class="collection-item">Empleado Departamento: {{Dpto}}</li>         

        </ul>
        <router-link to="/" class="btn grey"> Back </router-link>
        <button @click="deleteEmpleado" class="btn red">Delete </button>
        <div class="fixed-action-btn">
            <router-link v-bind:to="{name:'completeDir',params:{dir_id:empleado_id}}" class="btn-floating btn-large red">
            <i class="fa fa-pencil"/>
                </router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'editDir',
    data () {
        return {
            empleado_id: null,
            Nombre: null,
            Dpto: null            
        }
    },
    beforeRouteEnter (to, from, next) {
        db.collection('Empleados').where('empleado_id','==', to.params.empleado_id).get()
        .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.empleado_id = doc.data().empleado_id
                    vm.Nombre = doc.data().Nombre
                    vm.Dpto = doc.data().Dpto
                })
            } )
        }
        )
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData(){
            db.collection('Empleados').where('empleado_id','==',this.$route.params.empleado_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach(
                    doc => {
                        this.empleado_id = doc.data().empleado_id
                        this.Nombre = doc.data().Nombre
                        this.Dpto = doc.data().Dpto
                    }
                )
            })
        },
        deleteEmpleado(){
            if(confirm("Seguro?")){
                db.collection('Empleados').where('empleado_id','==',this.empleado_id).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        doc.ref.delete();
                        this.$router.push('/')
                    })
                })
            }
        }
    }
}
</script>


