<template>
    <div id="completeDir">
        <div class="row">
            <form @submit.prevent="updateEmpleado" class="col s12">
                <div class="row">
                    <div class ="input-field col s12">
                        <input disabled type="text" v-model="empleado_id" required> 
                        
                    </div>
                </div>
                   <div class="row">
                    <div class ="input-field col s12">
                        <input type="text" v-model="Nombre" required> 
                        
                    </div>
                </div>
                   <div class="row">
                    <div class ="input-field col s12">
                        <input type="text" v-model="Dpto" required>                         
                    </div>
                </div>
                <button type="submit" class="btn"> Submit </button>
                <router-link to="/" class="btn grey"> Cancel </router-link>
            </form>
        </div>
    </div>
</template>

<script>
import db from "./firebaseInit"
export default {
    name: 'completeDir',
    data () {
        return {
            empleado_id:null,
            Nombre:null,
            Dpto:null            
        }
    },
    beforeRouteEnter (to, from, next) {
        db.collection('Empleados').where('empleado_id','==', to.params.dir_id).get()
        .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.empleado_id = doc.data().empleado_id
                    vm.Nombre = doc.data().Nombre
                    vm.Dpto = doc.data().Dpto
                })
            } )
        }
        )
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData(){
            db.collection('Empleados').where('empleado_id','==',this.$route.params.dir_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach(
                    doc => {
                        this.empleado_id = doc.data().empleado_id
                        this.Nombre = doc.data().Nombre
                        this.Dpto = doc.data().Dpto
                    }
                )
            })
        },
        updateEmpleado(){
             db.collection('Empleados').where('empleado_id','==',this.$route.params.dir_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach(
                    doc => {
                        doc.ref.update({
                            empleado_id:this.empleado_id,
                            Nombre:this.Nombre,
                            Dpto:this.Dpto
                        }).then(() => {
                            this.$router.push({name:"editDir",params:{empleado_id:this.empleado_id}})
                        })
                    }
                )
            })
        }
    }
}
</script>


