import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '@/components/Dashboard'
import VueHl7 from '@/components/VueHl7'
import NewHL7 from '@/components/NewHL7'
import EditDir from '@/components/EditDir'
import Parser from '@/components/Parser'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'dashboard',
      component: Dashboard
    },
    {
      path: '/new',
      name: 'newHL7',
      component: NewHL7
    },
    {
      path: '/edit/:empleado_id',
      name: 'editDir',
      component: EditDir
    },
    {      
        path: '/parser',
        name: 'parser',
        component: Parser      
    },    
    {
      path: '/:dir_id',
      name: 'completeDir',
      component: VueHl7
    }
    
  ]
})



