<template>
    <div id="dashboard">
        <ul class="collection with-header">
            <li class="collection-header"> <h4> Empleados </h4> </li>
            <li v-for="empleado in empleados" v-bind:key="empleado.id" class= "collection -item"> <div class="chip"> {{empleado.empleado_id}} </div> {{empleado.Dpto}}:{{empleado.Nombre}}
            <router-link class= "secondary-content" v-bind:to="{name:'editDir', params: {empleado_id:empleado.empleado_id}}">
                <i class="fa fa-eye"/> </router-link>
             </li>
        </ul>

        <div>
            <router-link to="/parser" class="btn-floating btn-large green">
            <i class="fa fa-pencil"> </i>
            </router-link>
        </div>

        <div class="fixed-action-btn">
            <router-link to="/new" class="btn-floating btn-large red"> 
            <i class="fa fa-plus">  </i>
            </router-link>
            
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'

export default {
    name: 'dashboard',
   
    data () {
        return {
            empleados: []
        }
    },
    created () {
        db.collection('Empleados').orderBy("Nombre").get().then(querySnapshot => {
            querySnapshot.forEach(doc => { 
                console.log(doc.id);               
                const data = {
                    'id': doc.id,
                    'empleado_id':doc.data().empleado_id,
                    'Nombre': doc.data().Nombre,
                    'Dpto':doc.data().Dpto
                }
                this.empleados.push(data);
            })
        })

    }
}
</script>


