<template>
    <div id="parser">      
        <h3> PARSER </h3> 
        
        <input type="file" @change="readFile">
        <ul>
            <div v-for="(val,index) in valor" v-bind:key="index"> 
               <h4> {{val[0]}} </h4>
                <li v-for="(v,index) in val" v-bind:key="index"  > {{v}} </li>
            </div>
        </ul>           
        

          
    </div>
</template>
<script>
export default {
    
    name:'parser',   
    data () {
        return {
        dir:["x","y","z"],
        bandera:false,
        valor:[],
        campos:[]        
        }
    },
    methods: {
    readFile(ev) {  
        this.valor.push("a")
        let esto = this      
        let mensaje
        let auxiliar
        let x = 0
        const file = ev.target.files[0]
        const reader = new FileReader()        
        reader.readAsText(file)    
        reader.onload = function(e) {
            mensaje = reader.result.split("\n") 
            auxiliar = mensaje.slice()
            while(x < auxiliar.length)
            {
            mensaje[x] = auxiliar[x].split("|")
            x++
            } 
        esto.valor = mensaje              
                                
        }  
    }   
}
}
</script>



